<template>
  <section>
    <header><h1>My Friends</h1></header>
    <add-friend @add-friend="addFriend" />
    <ul v-for="friend in friends" :key="friend.name">
      <friend-contact
        :key="friend.id"
        :id="friend.id"
        :name="friend.name"
        :email="friend.email"
        :phone="friend.phone"
        :isFavourite="friend.isFavourite"
        @toggle-favourite="toggleFavourite"
      />
    </ul>
  </section>
</template>

<script>
export default {
  data() {
    return {
      friends: [
        {
          id: 'id1',
          name: 'juliet',
          email: 'juliet@email.com',
          phone: '7777777777',
          isFavourite: true,
        },
        {
          id: 'id2',
          name: 'romeo',
          email: 'romeo@email.com',
          phone: '6666666666',
          isFavourite: false,
        },
      ],
    };
  },
  methods: {
    toggleFavourite(friendId) {
      const foundFriend = this.friends.find((friend) => friend.id === friendId);
      foundFriend.isFavourite = !foundFriend.isFavourite;
    },
    addFriend(friend) {
      const newFrined = {
        id: Math.random().toString(),
        ...friend,
        isFavourite: true,
      };
      this.friends.push(newFrined);
    },
  },
};
</script>

